import "./App.css";
import React, { useEffect, useState } from 'react';
import Navbar from './components/navbar-page/navbar';
import Home from './components/home-1/frontpage';
import Home2 from './components/home-2/highquality';
import Why from './components/why-choose/whychoose';
import Product from './components/product-page/productpage';
import About from './components/about-us/about';
import Contact from './components/contact-page/contact';
import Foot from './components/footer/footer';
import Come from './components/mob-tab/maintainance-page/maintpg';

function App() {
  const Main_scrollToSection = (ids) => {
    const section = document.getElementById(ids);
    if (section) {
      section.scrollIntoView({ behavior: "smooth" });
    }
  };

  const [windowSize, setWindowSize] = useState({
    width: window.innerWidth,
    height: window.innerHeight
  });

  useEffect(() => {
    const handleResize = () => {
      setWindowSize({
        width: window.innerWidth,
        height: window.innerHeight
      });
    };

    window.addEventListener('resize', handleResize);

    // Clean up the event listener when the component unmounts
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  return (
    <div className="App">
      {/* <Navbar fun={Main_scrollToSection}></Navbar>
      <Home fun={Main_scrollToSection} />
      <Home2 />
      <Why />
      <Product />
      <About fun={Main_scrollToSection} />
      <Contact />
      <Foot fun={Main_scrollToSection} /> */}


      {
        (() => {
          if (windowSize.width >= 320 && windowSize.width <= 1023) { return <Come/> }//Mobile S - Tab
          else if (windowSize.width >= 1024) {
            return <>
              <Navbar fun={Main_scrollToSection}></Navbar>
              <Home fun={Main_scrollToSection} />
              <Home2 />
              <Why />
              <Product />
              <About fun={Main_scrollToSection} />
              <Contact />
              <Foot fun={Main_scrollToSection} />
            </>
          }// Laptop- 4k

        })()}
    </div>
  );
}

export default App;
