/* eslint-disable react-hooks/rules-of-hooks */
import React, { useEffect, useState } from "react";
import axios from "axios";
import ProductIoc from "../../Icons/output-onlinegiftools.gif";
import { Radio } from "react-loader-spinner";
import Productcss from "./product-page.module.css";

function laptop() {
    const [data, setData] = useState([]);
    const [isLoading, setIsLoading] = useState(true);

    // eslint-disable-next-line react-hooks/rules-of-hooks
    useEffect(() => {
        const fetchData = async () => {
            try {
                const response = await axios.get(
                    "https://wetech-code.github.io/json-storage/online-json-editor.json"
                );
                const jsonData = response.data;
                setData(jsonData);
                // setData([]);
                setIsLoading(false);
            } catch (error) {
                console.log("Error:", error);
                setData([]);
                setIsLoading(false); // Update loading state on error
            }
        };

        fetchData();
    }, []);

    return (
        <>
            <div className={Productcss.Base} id="Main_Product">
                <div className={Productcss.head}>
                    <div className={Productcss.brick}>
                        <h1 className={Productcss.maintext}>Products</h1>
                        <img className={Productcss.ico} src={ProductIoc} alt="" />
                    </div>
                </div>
                <div className={Productcss.content_store}>
                    {isLoading ? (
                        <Radio
                            visible={true}
                            height="80"
                            width="80"
                            ariaLabel="radio-loading"
                            wrapperStyle={{}}
                            wrapperClass="radio-wrapper"
                        />
                    ) : (
                        data.map((item, index) => (
                            <div key={index} className={Productcss.card}>
                                {console.log(data)}
                                <img src={item.link} alt="" />
                            </div>
                        ))
                    )}
                </div>
            </div>
        </>
    );
}

export default laptop;
