import React from 'react'
import Navbarcss from './navbar.module.css'

function navbar(props) {
    return (
        <header className={Navbarcss.Base}>

            <div className={Navbarcss.Logo_cont} onClick={() => {
                props.fun("Main_Home");
            }} >
                <h1 className={Navbarcss.logotxt}>Logo</h1>
            </div>

            <div className={Navbarcss.right_cont}>
                {/* <span className={Navbarcss.HY} >Home</span> */}
                <span className={Navbarcss.HY} onClick={() => {
                    props.fun("Main_Product");
                }}>Product</span>
                <span className={Navbarcss.HY} onClick={() => {
                    props.fun("Main_about");
                }}>About us</span>
                <span className={Navbarcss.HY} onClick={() => {
                    props.fun("Main_contact");
                }}>Contact</span>

            </div>

        </header>
    )
}

export default navbar