import React, { useState, useEffect} from 'react';
import axios from 'axios';
import Aboutcss from './about.module.css';
import ProductIoc from "../../Icons/about.gif";
import Vid from "../../Icons/pexels-angela-roma-7316967 (1080p).mp4";

function Aboutpage(props) {

//API call of an images
  const [currentIndex, setCurrentIndex] = useState(0);
  const [images, setImages] = useState([]);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await axios.get(
          "https://wetech-code.github.io/json-storage/online-json-editor.json"
        );
        const jsonData = response.data;
        setImages(jsonData);
        setIsLoading(false);
      } catch (error) {
        console.log("Error:", error);
        setImages([]);
        setIsLoading(false);
      }
    };

    fetchData();
  }, []);

  useEffect(() => {
    const nextSlide = () => {
      setCurrentIndex((prevIndex) => (prevIndex + 1) % images.length);
    };

    const slideshowInterval = setInterval(nextSlide, 3000);

    return () => {
      clearInterval(slideshowInterval);
    };
  }, [images.length]);

  if (isLoading) {
    return <div>Loading...</div>;
  }
  

  // const nextSlide = () => {
  //   setCurrentIndex((prevIndex) => (prevIndex + 1) % images.length);
  //   setImages((prevImages) =>
  //     prevImages.map((image, index) => ({
  //       ...image,
  //       fade: index === currentIndex || index === (currentIndex + 1) % images.length,
  //     }))
  //   );
  // };

  return (
    <>
      <div className={Aboutcss.Base} id='Main_about'>
        <header className={Aboutcss.head}>
          <div className={Aboutcss.brick}>
            <h1 className={Aboutcss.maintext}>About Us</h1>
            <img className={Aboutcss.ico} src={ProductIoc} alt="" />
          </div>
        </header>
        <section className={Aboutcss.content_store}>
          <div className={Aboutcss.container1}>
            <h1 className={Aboutcss.abt_title}>
              Excellence in Quality Siddharth International's Leading Packaging
              Solutions
            </h1>
            <div className={Aboutcss.paragraph}>
              <p>
                Siddharth International is growing in industry by providing the
                best range of Cosmetic Packaging, Bottles, Closures, Food
                Packaging and Pharma Packaging. Products passes through
                well-defined systems before they are finally send for delivery.
                <br />
                Quality is given utmost importance and we ensure that the
                associated clients are served with certified solutions. Strict
                checks over quality have enabled us to maintain highest quality
                standards in our products. Quality managed by us throughout in
                our production has enabled us to take lead over other
                competitors. Quality is the factor that has enabled us to win
                the loyalty of clients by delivering them the best solutions
                without falling short in terms of quality. Quality control not
                only helps in maintaining cordial relation with clients but also
                helps to reduce the production costs; we are very well aware of
                the fact and ensure that the same is followed by us.
              </p>
            </div>
            <div className={Aboutcss.btn}>
              <button className={Aboutcss.explore_btn} onClick={() => {
                props.fun("about-sec2");
              }}>
                Explore
              </button>
            </div>
          </div>
          <div className={Aboutcss.container2}>
          <div className={Aboutcss.image}>
              <img src={images[currentIndex].link} alt="#" />
            </div>
          </div>
        </section>
        <section className={Aboutcss.content2} id='about-sec2' >
          <div className={Aboutcss.video_content}>
            <div>
              <video src={Vid} className={Aboutcss.vidz} autoPlay muted loop>
                Your browser does not support the video tag.
              </video>
            </div>
          </div>
          <div className={Aboutcss.text}>
            <p className={Aboutcss.para}>
              We understand that quality is a critical factor in gaining the trust and loyalty of our clients. Therefore, we prioritize quality control measures to ensure that our clients receive the best solutions without compromising on quality. Our dedication to quality not only strengthens our relationship with our clients but also helps us streamline production processes and reduce costs.
            </p>
            <p className={Aboutcss.para}>
              At Siddharth International, we believe that delivering superior quality products is the cornerstone of our success. We continuously strive to meet and exceed the expectations of our clients by upholding the highest standards of quality throughout our operations.
            </p>
            <p className={Aboutcss.para}>
              If you have any specific inquiries or requirements regarding our products or services, please feel free to reach out to us. We look forward to serving you with excellence and providing you with the best solutions in the industry.
            </p>
          </div>
        </section>
      </div>
    </>
  );
}

export default Aboutpage;
